@use '../base/variables' as v;
@use '../base/mixins' as m;
@use "sass:color";

.ayuda {
    // --- Estilos del contenedor principal de la p√°gina de Ayuda ---
    background-color: v.$bg_page_base; // Color de fondo general de la p√°gina.
    padding: 4rem 2rem; // Espaciado interno (4rem arriba/abajo, 2rem a los lados).
    color: v.$white; // Color de texto por defecto para los elementos hijos.
    min-height: 80vh; // Altura m√≠nima para que ocupe una buena parte de la pantalla.
    display: flex; // Usa Flexbox para controlar la alineaci√≥n del contenido.
    justify-content: center; // Centra el contenido horizontalmente.
    align-items: flex-start; // Alinea el contenido en la parte superior.

    // --- Contenedor del contenido (la "tarjeta" que agrupa el texto) ---
    &__contenedor {
        background-color: color.adjust(v.$bg_page_base, $lightness: 5%); // Fondo un poco m√°s claro que el de la p√°gina para crear contraste.
        padding: 2.5rem; // Espaciado interno para que el texto no toque los bordes.
        border-radius: 1rem; // Bordes redondeados para un look m√°s suave.
        box-shadow: 0 5px 20px rgba(0, 0, 0, 0.4); // Sombra para dar un efecto de profundidad.
        width: 100%; // Ocupa todo el ancho posible.
        max-width: 800px; // L√≠mite de ancho para que no se estire demasiado en pantallas grandes.
    }

    // --- T√≠tulo principal: "Centro de Ayuda" ---
    &__titulo {
        font-family: v.$font_headings; // Fuente especial para t√≠tulos.
        font-size: clamp(2.8rem, 5vw + 1rem, 4.5rem); // Aumentado: M√≠nimo 2.8rem, ideal 5vw+1rem, M√°ximo 4.5rem.
        color: v.$white; // Color del texto.
        text-align: center; // Alineaci√≥n centrada.
        margin-bottom: 3rem; // Espacio por debajo del t√≠tulo.
    }

    // --- Subt√≠tulos como "Preguntas Frecuentes (FAQ)" o "Contacto de Soporte" ---
    &__subtitulo {
        font-family: v.$font_headings; // Misma fuente de t√≠tulos.
        font-size: 2.5rem; // Tama√±o de fuente (ya lo hab√≠as aumentado).
        color: v.$secundary; // Color de acento para que resalte.
        border-bottom: 2px solid v.$secundary; // L√≠nea inferior decorativa y de separaci√≥n.
        padding-bottom: 0.5rem; // Espacio entre el texto y la l√≠nea.
        margin-bottom: 2rem; // Espacio por debajo del subt√≠tulo.
    }

    // --- Bloque que agrupa un conjunto de preguntas o la secci√≥n de contacto ---
    &__seccion {
        margin-bottom: 3rem; // Espacio entre secciones (ej. entre FAQ y Contacto).
    }

    // --- Estilos generales para p√°rrafos y elementos de lista ---
    p,
    li {
        font-size: 1.5rem; // Tama√±o de fuente para el texto principal (ya aumentado).
        line-height: 1.7; // Altura de l√≠nea para mejorar la legibilidad.
        color: v.$whiteGray; // Un color de texto m√°s suave que el blanco puro.
    }

    // --- Estilos para los enlaces (como el email y los tel√©fonos) ---
    a {
        color: v.$secundary; // Mismo color de acento que los subt√≠tulos.
        text-decoration: none; // Quita el subrayado por defecto.

        &:hover {
            text-decoration: underline;
        }

        // A√±ade el subrayado solo al pasar el rat√≥n.
    }

    // --- Estilos para cada bloque de pregunta y respuesta individual ---
    &__pregunta {
        margin-bottom: 1.5rem; // Espacio entre cada pregunta.

        h3 {
            font-size: 1.9rem; // Tama√±o de la pregunta (ya aumentado).
            font-weight: 700; // Texto en negrita para que destaque.
            color: v.$white; // Color blanco para la pregunta.
            margin-bottom: 0.5rem; // Espacio entre la pregunta y su respuesta.
        }
    }
}